import React, { useState} from 'react';
import Layout from '../components/layout';
import SEO from '../components/seo';
import SectionTitles from '../components/shared/section-titles';
import constants from '../constants/shops';
import GoogleMapComponent from '../components/shared/google-map';

const ShopPage = ({ }) => {
  const [selectedCity, setSelectedCity] = useState(0);

  return (
    <>
      <Layout>
        <div style={{ paddingTop: '12rem' }}>
          <SEO title={'Магазини'} />
          <SectionTitles>
            <h1>Нашите офиси в цяла<br />България</h1>
            <hr className='center' />
          </SectionTitles>

          <div className='container text-center mt-5'>
            <h3>Бърз достъп</h3>

            <div className='row d-flex justify-content-center'>
              {constants.shops.map(({ city }, index) => (
                <div className='col-4 col-md-3 col-lg-2' key={index}>
                  <button className='clear'
                    onClick={() => {setSelectedCity(index)}}
                  >
                    <p>{city}</p>
                  </button>
                </div>
              ))}
              <hr className='big-blue' />

              {constants.shops[selectedCity].places.map((place, index) => (
                <>
                  {index !== 0 && (<hr className='small-blue' />)}
                  <Place {...place} />
                </>
              ))}

            </div>
          </div>
        </div>
      </Layout>
    </>
  );
}

const Place = ({ city, address, telephone, working_hours, center, officeFeatures, easyPayOffice }) => {
  return (
    <>
      <div className='row w-100'>
        <div className='col-12 col-md-6 text-left'>
          <h2>{city}</h2>
          <div className='my-4'>
            <h3><span className='icon map-pin-shop mr-2'></span>Адрес:</h3>
            <p className='margin-left-offices'>{address}</p>
          </div>
          <div className='my-4'>
            <h3><span className='icon blue-phone mr-2'></span>Телефон:</h3>
            <p className='margin-left-offices'>{telephone}</p>
          </div>
          <div className='my-4'>
            <h3><span className='icon clock mr-2'></span>Работно време:</h3>
            <div className='margin-left-offices'>
              {working_hours}
            </div>
          </div>
        </div>

        <div className='col-12 col-md-6'>
          <GoogleMapComponent height={350} center={center} />
          <div className='row mt-2'>
            <div className='col-6 m-0 p-0 d-flex justify-content-start'>
              <div>
                <h3><span className='icon map-pin-dark mr-2'></span>Офис на NetSurf:</h3>
                <ul className='blue-checks text-left'>
                  {officeFeatures.map((feature, index) => (
                    <li className='blue-checks' key={index}>
                      <p className='m-0 p-0'>{feature}</p>
                    </li>
                  ))}
                </ul>
              </div>
            </div>
            <div className='col-6 m-0 p-0 d-flex justify-content-start'>
              <div>
                <h3><span className='icon easypay mr-2'></span>Офис на Easypay:</h3>
                <ul className='blue-checks'>
                  {easyPayOffice.map((feature, index) => (
                    <li className='blue-checks' key={index}>
                      <p>{feature}</p>
                    </li>
                  ))}
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default ShopPage;